.App {
  position: relative;
  text-align: center;
  min-height: 100vh;
}

input {
  margin-left: 0;
  margin-right: 0;
  padding-left: 0;
  padding-right: 0;
}

input:focus {
   outline: none;
   border: none;
   text-decoration: underline;
}

h3 {
  color: #000000;
}

*{
  font-family: 'Work Sans', sans-serif;
}

a {
  color: inherit;
  text-underline: #000000;
  font-weight: bold;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
